@import './mixins/mixins';
@import './mixins/variables';


.section-header {
    .header-topbar {
        height: auto;
        .container {
            height: 100%;
            .header-topbar-content {
                display: flex;
                align-items: center;
                justify-content: space-between;
                height: 100%;
                padding: 16px 0;
                h1 {
                    font-size: 14px;
                    line-height: 189%;
                    letter-spacing: 0.1px;
                    color: #FFFFFF;
                    margin: 0;
                }
                .connect {
                    display: flex;
                    align-items: center;
                    a {
                        color: #FFFFFF;
                        text-transform: uppercase;
                        font-size: 14px;
                    }
                    .language {
                        margin-left: 24px;
                        display: flex;
                        align-items: center;
                    }
                    .connect-faq , .connect-contact {
                        margin-left: 30px;
                    }
                }
            }

            .header-topbar-logo {
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 21px 0;
            }
        }
    }

    .header-main {
        .container {
            .header-main-content {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 12px 0;
                .btmenu {
                    display: none;
                    width: 26px;
                    height: 20px;
                    position: relative;
                    z-index: 999;
                    span {
                        width: 100%;
                        height: 3px;
                        display: block;
                        background: black;
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                    &::before , &::after {
                        content: "";
                        width: 100%;
                        height: 3px;
                        display: block;
                        background: black;
                        transform: rotate(0deg);
                        transition: 0.3s;
                    }
                    &::after {
                        position: absolute;
                        bottom: 0;
                    }
                    &.clicked {
                        &::before {
                            @include transform(rotate(45deg) translate(8px,8px));
                            transition: 0.3s;
                        }
                        span {
                            opacity: 0;
                        }
                        &::after {
                            @include transform(rotate(-45deg) translate(5px,-8px));
                            transition: 0.3s;
                        }
                    }
                }
                .nav {
                    ul {
                        display: flex;
                        align-items: center;
                        li {
                            a {
                                padding: 0 20px;
                                color: #71767C;
                                text-transform: uppercase;
                                font-size: 14px;
                                &:hover {
                                    color: #0F1214;
                                }
                            }
                        }
                    }
                }
                .content-right {
                    display: flex;
                    align-items: center;
                    .search , .wishlist , .cart {
                        display: flex;
                        align-items: center;
                        p {
                            margin-left: 8px;
                            text-transform: uppercase;
                            font-size: 14px;
                            letter-spacing: 0.1px;
                            color: #494F56;
                        }
                    }
                    .cart {
                        margin-left: 20px;
                    }
                    .wishlist {
                        margin-left: 12px;
                    }
                }
            }



            .header-home-content {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px 0 20px;
                .content-left {
                    display: flex;
                    align-items: center;
                    .login , .wishlist , .cart {
                        display: flex;
                        align-items: center;
                        p {
                            margin-left: 8px;
                            text-transform: uppercase;
                            font-size: 14px;
                            letter-spacing: 0.1px;
                            color: #494F56;
                        }
                    }
                    .cart {
                        margin-left: 20px;
                    }
                    .login {
                        margin-left: 12px;
                    }
                }
                .nav {
                    ul {
                        display: flex;
                        align-items: center;
                        li {
                            a {
                                padding: 0 20px;
                                color: #71767C;
                                text-transform: uppercase;
                                font-size: 14px;
                                &:hover {
                                    color: #0F1214;
                                }
                            }
                        }
                    }
                }
                .search {
                    position: relative;
                    input {
                        width: 310px;
                        border-bottom: 1px solid #F4F4F5;
                        padding: 10px 0px;
                        height: 30px;
                    }
                    span {
                        display: flex;
                        align-items: center;
                        position: absolute;
                        right: 0;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }
            }
        }
    }



    @include desktop {
        .header-topbar {
            .container {
                .header-topbar-content {
                    h1 {
